<template>
<div>
    <h1 class="font-weight-light">{{authUser.fullName}}</h1>

    <div class="card mb-4 mt-4 bg-white shadow-material">
        <div class="card-body">
            <strong>Логин:</strong> {{authUser.login}} <br>
            <strong>Роль:</strong> {{authUser.role}} <br>
            <strong>Тарифный план:</strong> {{authUser.tariff}} <br><br>
            <button type="button" class="btn btn-outline-danger" @click="logout()">Выйти</button>
        </div>
    </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed:{
    ...mapGetters(['authUser']),
  },
  methods:{
    logout(){
      localStorage.removeItem('jwtToken')
      this.$router.go()
    }
  }
}
</script>